

































import { Component, Emit, Vue, Prop } from "vue-property-decorator";
@Component
export default class LoginForm extends Vue {
  @Prop(Boolean) isLoading!: boolean;
  acc: string = "";
  pwd: string = "";

  @Emit("doLogin")
  doLogin() {
    const acc = btoa(this.acc);
    const pwd = btoa(this.pwd);
    return { acc, pwd };
  }
}
