































































import { Component, Vue } from "vue-property-decorator";
import LoginForm from "@/components/LoginForm.vue";
import { apiLogin, apiPunch } from "@/utils/api";

@Component({
  components: { LoginForm }
})
export default class LoginCard extends Vue {
  isLoading: boolean = false;
  isLogin: boolean = false;
  isPunching: boolean = false;
  loginFail: boolean = false;
  clockIn: string = "";
  clockOut: string = "";
  acc: string = "";
  pwd: string = "";

  buttonClass(checkString: string): string {
    if (checkString.length === 8) {
      return "success";
    } else {
      return "primary";
    }
  }

  get clockInButtonString(): string {
    let str = "不先打上班？";
    if (this.clockIn.length === 8) {
      str = this.clockIn;
    }
    return str;
  }

  get clockOutButtonString(): string {
    let str = "下班";
    if (this.clockIn.length === 0) {
      str = "<-";
    }
    if (this.clockIn.length === 8) {
      str = "打卡打起來";
    }
    if (this.clockOut.length === 8) {
      str = this.clockOut;
    }
    return str;
  }

  login(params: { acc: string; pwd: string }) {
    this.loginFail = false;
    this.isLoading = true;
    const { acc, pwd } = params;
    this.acc = acc;
    this.pwd = pwd;
    apiLogin({ acc, pwd }).then(res => {
      if (!res.hasOwnProperty("message")) {
        this.isLogin = true;
        this.clockIn = res.clockin.slice(-8).trim();
        this.clockOut = res.clockout.slice(-8).trim();
      } else {
        this.loginFail = true;
      }
      this.isLoading = false;
    });
  }

  // 上班:1 下班:2
  punch(inout: string): void {
    const { acc, pwd } = this;
    this.isPunching = true;
    apiPunch({ acc, pwd, inout }).then(res => {
      this.isPunching = false;
      this.clockIn = res.clockin.slice(-8).trim();
      this.clockOut = res.clockout.slice(-8).trim();
    });
  }
}
