import axios from "axios";

const domain = "https://xteam-hack.herokuapp.com";
// const domain = "http://localhost:3000";
const apiExecutor = axios.create({
  baseURL: domain
});

export const apiBootService = () => apiExecutor.get("/").then(res => res.data);

export const apiLogin = (data: { acc: string; pwd: string }) =>
  apiExecutor.post("/getClock", data).then(res => res.data);

export const apiPunch = (data: { acc: string; pwd: string; inout: string }) =>
  apiExecutor.post("/punch", data).then(res => res.data);
