



























import { Component, Prop, Vue, Emit } from "vue-property-decorator";
import axios from "axios";
import { apiBootService } from "@/utils/api";

@Component
export default class DefaultCard extends Vue {
  @Prop(Boolean) status!: boolean;

  @Emit("update:status")
  onInputAccount(status: boolean): boolean {
    return status;
  }

  mounted() {
    this.boots();
  }

  boots() {
    apiBootService().then(res =>
      this.onInputAccount(res.hasOwnProperty("message"))
    );
  }
}
